.wrapper--entertainment {
	max-width: 1420px;
	margin: 0 auto; }

.entertainment__slider {
	margin-top: 110px;

	@include bp(1100px) {
		margin-top: 80px; }

	img {
		width: 100%; }
	.swiper-pagination {
		width: 100%;
		position: absolute;
		bottom: 18px;
		left: 0;
		display: flex;
		justify-content: center;
		z-index: 3;
		&-bullet {
			width: 6px;
			height: 6px;
			border-radius: 50%;
			margin: 0 3px;
			background-color: $white;
			opacity: 0.8;
			cursor: pointer;

			&.swiper-pagination-bullet-active {
				transform: scale(0.7); } } }

	@include bp(1100px) {
		.navigation-arrows--center {
			display: none; } } }

.description_slider {
	display: flex;
	justify-content: space-between;

	h3 {
		margin-top: 45px; }
	p {
		margin: 45px 0 0 0;
		color: $gray4;
		width: 890px; }

	@include bp(1350px) {
		h3 {
			width: 435px; }
		p {
			padding-left: 92px; } }
	@include bp(1100px) {
		h3 {
			width: 649px; } }
	@include bp(1024px) {
		flex-direction: column;

		h3 {
			width: 100%; }
		p {
			padding: 0;
			margin: 0;
			width: 100%; } } }


.services-entertainment {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -45px 110px;

	@include bp(1100px) {
		margin: 0 -15px 80px; }
	&__column {
		width: 33.3%;
		padding: 0 45px;
		margin-top: 110px;

		@include bp(1100px) {
			margin-top: 60px; }

		&.w50 {
			width: 50%; }

		@include bp(1100px) {
			padding: 0 15px; }
		@include bp(1023px) {
			width: 100%;

			&.w50 {
				width: 100%; } } }
	&-item {
		display: block;
		background-color: white;
		width: 100%;
		height: 100%; }
	a {
		border: none;
		img {
			width: 100%; } }

	.entertainment__slider {
		margin: 0;
		img {
			height: 100%;
			@include bp(1024px) {
				width: 100%; } }
		@include bp(1100px) {
			.navigation-arrows--center {
				display: none; } } } }

.services-item_description {
	h5 {
		color: $black;
		margin: 30px 0 16px; }
	p {
		font-size: 16px;
		line-height: 140%;
		color: $gray4;
		margin: 0; }
	&__w50 {
		display: flex;

		h5 {
			color: $black;
			width: 200px;
			margin: 30px 45px 0 0; }
		p {
			width: 400px;
			margin: 35px 0 0 0;
			font-size: 16px;
			line-height: 140%;
			color: $gray4; }

		@include bp(1100px) {
			flex-direction: column; }
		@include bp(1023px) {
			h5,p {
				width: 100%; }
			p {
				margin-top: 25px; } } } }

