.calendar-select {
	position: relative;


	&__inputs {
		display: flex;
		width: 100%;
		position: absolute;
		top: -40px;
		left: 0; }

	&__select {
		display: flex;
		align-items: center;
		width: 100%;
		height: 55px;
		background-color: $gray2;
		font-size: rem(16px);
		font-weight: bold;
		padding: 0 20px 0 15px;
		position: relative;
		cursor: pointer;

		&:after {
			content: '';
			width: 10px;
			height: 6px;
			background: url('../images/arrow.svg') no-repeat;
			@include center('v');
			right: 20px; } }

	&__dropdown {
		display: none;
		position: absolute;
		left: 0;
		top: 65px;
		width: 100%;
		border: 1px solid $gray3;
		background-color: $white;
		padding: 25px 20px 20px;
		box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.1);
		z-index: 3;
		&:after {
			content: '';
			display: block;
			background: url('../images/icons/arrow3.png') no-repeat;
			width: 22px;
			height: 15px;
			@include center('h');
			top: -13px; } }

	&__list {
		display: flex;
		padding-bottom: 25px;
		position: relative;
		margin: 0 -4px;
		&:after {
			content: '';
			height: 1px;
			position: absolute;
			left: -15px;
			right: -15px;
			bottom: 0;
			background: $gray2; }

		@include bp($point-4) {
			display: block;
			margin: 0; } }

	&__item {
		padding: 0 4px;
		width: calc(100% - 95px);
		&:nth-child(2) {
			width: 103px; }

		@include bp($point-4) {
			padding: 0;
			width: 100%;
			margin-bottom: 8px;
			&:nth-child(2) {
				width: 100%;
				margin-bottom: 0; } } }


	&__month {
		padding-right: 5px; }

	&__button {
		text-align: right;
		padding-top: 25px;

		.btn {
			min-width: 165px; }

		@include bp($point-4) {
			.btn {
				width: 100%; } } } }
