.calendar-baner {
	padding: 25px 0 40px;
	&__part {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		background-color: $gray1;
		padding: 40px 50px 39px; }
	&__title {
		display: flex;
		align-items: center;

		h3 {
			margin: 0;
			max-width: 247px; } }

	&__icon {
			width: 74px;
			height: 74px;
			margin-right: 70px; }

	&__desc {
		max-width: 326px;
		margin: 0 30px;
		transform: translateX(-17%);
		margin-top: 5px;
		p {
			margin: 0; } }
	&__button {
		margin-top: 10px;
		.btn {
			min-width: 180px; } }

	@include bp($point-5) {
		padding-top: 15px;
		padding-bottom: 37px;
		&__desc {
			transform: none; }
		&__icon {
			margin-right: 38px; }

		&__part {
			display: block;
			padding-left: 165px;
			position: relative;
			padding: 50px 50px 50px 165px; }
		&__icon {
			position: absolute;
			left: 50px;
			margin-right: 0;
			margin-top: 20px; }
		&__title {
			h3 {
				max-width: 100%; } }
		&__desc {
			margin: 20px 0 25px;
			max-width: 426px; }
		&__button {
			margin-top: 0; } }

	@include bp($point-2) {
		padding-top: 0;
		padding-bottom: 25px;

		&__part {
			padding: 40px 30px 30px; }
		&__icon {
			position: static;
			margin-bottom: 40px; }
		&__title {
			display: block; }
		&__button {
			.btn {
				width: auto;
				min-width: 0; } } } }

