.form-select {
	position: relative;
	&__label {
		position: absolute;
		left: 20px;
		top: 8px;
		z-index: 20;
		font-weight: 500;
		font-size: $select-label-fs;
		line-height: $select-label-lh;
		color: $input-color; }

	&[disabled],
	&.disabled {
		opacity: .5;
		pointer-events: none; }

	&--large {
		.select2-container {
			.select {
				height: 55px;

				@include bp($point-2) {
					height: 45px; } } } }

	&--gray {
		.select2-container {

			.select.select2-selection--single {
				background: $gray2;
				border-color: $gray2;

				.select2-selection__rendered {
					font-weight: 700; } }

			.select.select2-selection--single .select2-selection__arrow {
				right: 15px; } }


		.select2-container--open {
			.select:after {
				display: none; } } } }



.select2-container {
	* {
		outline: none!important; }
	.no-input {
		.select2-search {
			display: none; } }

	.input {
		margin: 0;
		.select2-search {
			font-size: $input-fs;
			color: $input-color;
			padding: 20px 20px 0;

			&__field {
				border-radius: $input-border-radius;
				border-color:  $input-bor-color;
				height: 48px;
				padding-left: 20px; } } }
	.select {
		height: $input-h;
		display: flex;
		align-items: center;
		border-radius: 0px;

		&.select2-selection--single {
			// border-radius: $input-border-radius
			border: 1px solid $input-bor-color;

			@include bp($point-2) {
				border-radius: $input-border-radius-xs; }


			.select2-selection__rendered {
				margin: 0px 0 0;
				padding-left: 20px;
				color: $input-color;
				font-size: $input-fs;
				overflow: visible;

				@include bp($point-2) {
					font-size: $input-fs-xs; } }

			.select2-selection__arrow {
				top: 0;
				bottom: 0;
				right: 20px;
				margin: auto;
				height: 6px;
				width: 10px;
				b {
					width: 100%;
					height: 100%;
					background: url('../images/arrow.svg') 50% no-repeat;
					// background-size: contain
					border: unset;
					transition: .3s linear; } } }

		@include bp($point-2) {
			height: $input-h; } }

	.select2-selection__placeholder {
		display: block;
		transform-origin: 0 0; }

	&--default {
		border-radius: 0px;
		.select2-selection--single {
			.select2-selection__placeholder {
				color: $baseTxt; } } }

	&--open {
		.select {
			border-bottom-color: transparent!important;
			position: relative;

			&:after {
				content: '';
				position: absolute;
				left: 0;
				right: 0;
				bottom: 0;
				margin: 0 auto;
				width: calc(100% - 40px);
				height: 1px;
				background-color: $input-bor-color; } }

		.select2-dropdown {
			border-color: $gray3;

			&--below {
				// border-top: 1px solid $gray3
				// border-top-color: transparent
				border-bottom-left-radius: $input-border-radius;
				border-bottom-right-radius: $input-border-radius;
				box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.1); }
			&--above {
				border-color: $input-bor-color;
				border-bottom-color: transparent;
				border-top-left-radius: $input-border-radius;
				border-top-right-radius: $input-border-radius; } }

		.select2-selection--single {
			.select2-selection__arrow {
				b {
					transform: scaleY(-1);
					transform-origin: 50%; } } }

		&.select2-container--above {
			.select {
				border-top-color: transparent!important;
				border-bottom-color: $input-bor-color!important;

				&:after {
					bottom: unset;
					top: 0; } } } }

	.select2-results {
		.select2-results__options {
			padding: 10px 0;

			&::-webkit-scrollbar {
				width: 1px; }

			&::-webkit-scrollbar-track {
				background: transparent; }

			&::-webkit-scrollbar-thumb {
				background: $gray3;
				margin-right: 3px; }

			&::-webkit-scrollbar-thumb:hover {
				background: transparent; }

			.select2-results__option {
				margin: 0px 0!important;
				padding: 8px 20px;
				font-size: $select-option-fs;
				background-color: transparent;
				color: $input-color;

				&:hover {
					background: $gray1;
					color: $input-color; }

				&:first-child {
					margin-top: 0!important; }

				&:last-child {
					margin-bottom: 0!important; }

				&:after {
					display: none; }

				// &--highlighted
				// 	color: $select-option-color-active

				@include bp($point-2) {
					font-size: $select-option-fs-xs; } } } } }


.js-select + .select2-container, .js-select-input + .select2-container {
	width: 100%!important; }
