.swiper-pagination {
    width: 100%;
    position: absolute;
    bottom: 18px;
    left: 0;
    display: flex;
    justify-content: center;
    z-index: 3; }

.swiper-pagination-bullet {
    width: 6px;
    height: 6px;
    border-radius: 50%;
    margin: 0 3px;
    background-color: $white;
    opacity: 0.8;
    cursor: pointer;

    &.swiper-pagination-bullet-active {
        transform: scale(0.7); } }
