.order-block {
	@include flex_between_center;
	flex-wrap: wrap;
	padding: 32px 40px;
	@include bp($point-4) {
		padding: 15px 0; }

	&__container {
		display: flex;
		justify-content: space-between;
		@include bp(+640px) {
			flex-direction: column; } }
	&__main {
		width: 100%;
		padding-bottom: 60px;
		@include bp(+640px) {
			padding-bottom: 15px;
			padding-top: 40px; } }
	&__sidebar-order {
		flex-shrink: 0;
		width: 305px;
		margin-left: 40px;
		@include bp(+640px) {
			display: flex;
			flex-direction: column;
			width: 100%;
			margin-left: 0; } }
	&--border-orange {
		border: solid 7px $orange;
		border-style: solid;
		border-width: 7px;
		border-image: url('../images/pattern.jpg') 7 repeat repeat;
		@include bp($point-4) {
			padding: 15px 25px; } }
	&__title {
		h5 {
			margin-top: 0; }
		p {
			font-size: rem(16px);
			@include bp($point-5) {
				font-size: rem(12px); } }
		&--card {
			padding-right: 200px;
			background-image: url('../images/cards-orderBG.png');
			background-repeat: no-repeat;
			background-position: top right;
			background-size: 160px 80px;
			@include bp($point-5) {
				padding-right: 0;
				padding-top: 100px;
				background-position: top center;
				background-size: 160px 80px; } } }
	&__title-wrapper {
		@include flex_between_center;
		@include bp($point-5) {
			flex-direction: column;
			align-items: start; } }
	&__form-control {
		margin-bottom: 30px; }
	&__field-wrapper {
		margin-top: 20px;
		&--transport {
			flex-grow: 1;
			max-width: 620px;
			@include bp($point-5) {
				max-width: none;
				width: 100%; } } }
	&--transport {
		@include bp($point-5) {
			flex-direction: column-reverse;
			align-items: start; } } }


.form-control {
	@include flex_between_center;
	flex-shrink: 0;
	@include bp(+640px) {
		justify-content: space-between;
		width: 100%; }
	&__text {
		padding-left: 20px;
		font-size: rem(16px);
		@include bp($point-5) {
			padding-left: 0; }
		@include bp(+640px) {
			font-size: rem(14px); } }
	&__wrapper {
		@include flex_between_center;
		width: 145px;
		margin-left: 20px;
		@include bp(+640px) {
			width: 95px; } }

	&__button {
		@include flex_center_b;
		width: 45px;
		height: 45px;
		background-color: $gray2;
		font-size: rem(30px);
		font-weight: bold;
		cursor: pointer;
		@include bp(+640px) {
			width: 30px;
			height: 30px;
			font-size: rem(24px); }
		&:hover {
			transform: scale(1.1); }
		&:active {
			transform: scale(0.9); } }
	&__number {
		@include bp(+640px) {
			font-size: rem(14px); } }
	&--transport {
		align-self: start;
		margin-top: 10px; } }

.not-active-order {
	opacity: 0.4;
	cursor: default;
	&:active {
		transform: scale(1); } }

.sidebar-order {
	&__wrapper {
		margin-bottom: 5px;
		padding-top: 0;
		padding-right: 26px;
		padding-bottom: 26px;
		padding-left: 26px;
		background-color: $gray2;
		border: solid 1px $gray2;
		&--button {
			@include bp(+640px) {
				background-color: transparent;
				border: solid 1px transparent;
				order: 2; } } }

	&__btn {
		margin: 20px 0; } }


// Сброс базовых стилей таблиц

.sidebar-order__wrapper {
	table {
		// table-layout: fixed
		min-width: auto;
		th {
			min-height: auto;
			padding: 0;
			span {
				display: block;
				font-size: inherit;
				line-height: inherit;
				color: $baseTxt; } }
		td {
			padding: 0;
			min-height: auto; }
		th {
			border: none; }
		td,tr,th {
			border: none;
			font-size: inherit;
			text-align: center;
			color: inherit;
			p {
				font-size: inherit; } }
		td {
			p {
				margin: 0; } } }
	div {
		margin: 0;
		color: inherit; } }

// Переопределение базовых стилей
.sidebar-order__wrapper {
	table {
		td {
			padding-top: 9px;
			text-align: left; }
		td.sidebar-order__cell--right {
			text-align: right; } } }

// Стили таблиц по классам
.sidebar-order {
	&__table {
		font-size: rem(16px);
		&--title {
			margin-top: 30px;
			font-size: rem(18px);
			font-weight: bold; }
		&--title-total {
			@include bp(+640px) {
				display: none; } } }
	&__cell {
		padding-top: 9px; } }


