.calendar {
	padding: 100px 0 25px;

	&__form-desctop {
		display: block; }

	&__form-mobile {
		display: none; }

	&__top {
		@include flex_between_center;
		margin-bottom: 45px;

		h2 {
			margin: 0; } }

	&-select {
		width: 288px;
		margin-left: 30px;
		margin-bottom: -19px; }

	&__line {
		display: none;
		&:first-child {
			display: block; } }
	&__line-wrap {
		display: flex;
		flex-wrap: wrap;
		padding-top: 25px; }


	&-item {
		margin-right: 30px;
		margin-bottom: 20px; }

	&__bowl {
		display: flex;
		justify-content: space-between;
		padding-bottom: 10px;
		border-bottom: 1px solid $gray2;
		position: relative; }

	&-category {
		display: flex;


		&__item {
			margin-right: 40px;
			margin-bottom: 20px;
			&:last-child {
				margin-right: 0; } } }

	&__form-search {
		position: relative; }


	&-content {
		padding-top: 30px;
		padding-bottom: 40px; }


	&-list {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -25px; }

	&-grid-item {
		width: 33.33%;
		padding: 0 25px;
		margin-bottom: 50px; }

	&-btn-filter {
		display: none; }


	&-block {
		display: block;
		color: $baseTxt;
		border: none;
		background-color: $gray1;

		&__desc {
			padding: 40px 40px 20px;

			time {
				display: block;
				font-weight: 700;
				font-size: rem(16px);
				color: $orange; }

			h4 {
				margin: 25px 0 0; }
			p {
				margin: 22px 0 20px; } }

		&__image {
			margin-bottom: -14px; } }


	@include bp(1024px) {
		&-category {
			&__item {
				margin-right: 30px; } } }


	@include bp($point-4) {
		padding-top: 55px;
		&-grid-item {
			width: 50%; }

		&__top {
			display: block;
			h2 {
				max-width: 450px; } }

		&-select {
			margin-left: auto;
			margin-top: 30px;
			max-width: 220px; }

		&-tab {
			position: relative; }

		&-category {
			width: 100%;
			justify-content: space-between;
			&--time {
				max-width: calc(100% - 250px);
				width: auto;
				position: absolute;
				left: 0;
				top: -157px; }

			&__item {
				margin-right: 40px; } } }


	@include bp(750px) {
		&-category {
			&__item {
				margin-right: 20px; } }
		&-list {
			margin: 0 -10px; }

		&-grid-item {
			padding: 0 10px;
			margin-bottom: 20px; }

		&-block {
			&__desc {
				padding: 30px;

				p {
					margin-bottom: 0px; } } } }

	@include bp(600px) {
		padding-top: 40px;

		&-search {
			display: flex;
			justify-content: space-between; }
		&-btn-filter {
			width: 45px;
			height: 45px;
			@include flex_center_b;
			border: 1px solid $gray3;
			cursor: pointer;
			transition: 0.3s;

			svg {
				width: 23px;
				height: 23px;
				stroke: transparent;
				fill: $gray3;
				transition: 0.3s; }

			&:hover,
			&.active {
				border-color: $orange;
				svg {
					fill: $orange; } } }

		&__form-search {
			width: calc(100% - 52px); }

		&__form-search {
			.form-field {
				margin-bottom: 0;
				&__input {
					padding-left: 14px; } } }

		&__form-desctop {
			.calendar-tab,
			.calendar-select {
				display: none; } }

		&__form-mobile {
			display: block;
			width: 100%;
			height: 100vh;
			background-color: $white;
			position: fixed;
			top: 0;
			left: 0;
			z-index: 300;
			transform: translateY(-130%);
			transition: 0.7s ease-in-out;
			&.active {
				transform: translateY(0); }
			.calendar {
				&-btn-filter {
					svg {
						width: 19px;
						height: 19px; } }

				&-category {
					display: block;

					&__roll {
						padding: 20px 14px 0px;
						display: none; }

					.calendar-category__item {
						margin-bottom: 10px; } }

				&-category--time {
					display: flex;
					position: static;
					max-width: 100%;
					justify-content: flex-start;
					.calendar-category__item {
						margin-right: 30px;
						margin-bottom: 0;
						&:last-child {
							margin-right: 0; } } } } }

		&-modal {
			width: 100%;
			height: calc(100vh - 120px);
			overflow-y: auto;
			padding: 25px 20px;

			&__time {
				padding: 12px 14px 18px; }

			&__category {
				padding: 0 14px; }

			&__btn {
				position: fixed;
				left: 34px;
				bottom: 70px;
				width: auto;
				min-width: 157px; } }

		&-select {
			max-width: 100%;
			width: 100%;
			margin-top: 5px;
			margin-bottom: 25px;
			&__select {
				height: 45px;
				padding-left: 12px; } } }




	@include bp($point-2) {
		&__top {
			margin-bottom: 30px;
			h2 {
				max-width: 260px; } }

		&-grid-item {
			width: 100%; } } }
