article.news-block {
	padding: 60px 0; }

.nav-news-block {
	margin: 30px 0; }

.nav-list li {
	position: static;
	list-style: none;
	margin: 0; }

.nav-bar-news-item:not(:first-child):before {
	content: "/";
	padding: 0 15px; }

.link--gray-news-disabled {
	font-size: $link-fs;
	color: $gray4; }

.news-block-title {
	max-width: 1105px; }

.news-block-description {
	max-width: 1040px; }

.news-block-content {
		margin: 65px 0 0;

		img {
			width: 100%; } }
.slider-block {
	padding-top: 60px; }

.wrapper-slider-block-news {
	position: relative;
	margin-bottom: 80px;
	h2 {
		margin: 0 0 5px; } }

.news-slider {
	overflow: hidden;
	margin-top: 45px;
	width: 100%; }

.slider-block-news_description {
	@include flex_between_center;
	.navigation-arrows {

		@include bp($point-3) {
			display: none; } } }

@media screen and (max-width: 480px) {
	.nav-news-block ul li:not(:last-child) {
		margin:  0; }

	.slider-block {
		padding-top: 20px; }

	.news-block-content {
		margin: 0; }

	.wrapper-slider-block-news {
		margin-bottom: 0; }

	article.news-block {
		padding: 0 0 20px; }

	.wrapper-slider-block-news h2 {
		margin: 0; }

	.news-slider {
		margin-top: 30px;
		max-width: 80%;
		margin-left: 0;
		overflow: visible; } }
