.tooltipster-shadow-customized.tooltipster-shadow {

	.tooltipster-box {
		box-shadow: 0 2px 4px rgba(33, 36, 41, 0.05);
		background-color: transparent; }

	.tooltipster-content {
		background-color: $tooltip-bg;
		border-radius: $tooltip-bor-radius;
		padding: 13px 24px;
		color: $baseTxt;
		font-family: $baseFont;
		font-size: 14px;
		line-height: 140%;
		border: 1px solid $gray3;
		max-width: 280px; }

	.tooltipster-arrow {
		.tooltipster-arrow-border {
			border-top-color: transparent; } } }


.tool-icon {
    display: inline-block;
    position: absolute;
    right: -18px;
    top: -22px;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background-color: $gray4;
    cursor: pointer;
    transition: 0.3s;
    z-index: 2;

    &:after {
        content: '?';
        font-size: rem(12px);
        color: $white;
        @include center('both'); }

    &:hover {
    	background-color: $orange;

    	.tool-window {
    		opacity: 1;
    		pointer-events: auto; } } }


.tool-window {
	display: block;
	width: 280px;
	background-color: $white;
	border: 1px solid $gray6;
	padding: 25px 20px 30px 27px;
	position: absolute;
	top: 35px;
	left: 50%;
	transform: translateX(-50%);
	opacity: 0;
	pointer-events: none;
	transition: 0.5s ease-in-out;
	box-shadow: 0 5px 14px rgba(33,36,41,.1);
	&:before {
		content: url('../images/icons/arrow2.png');
		position: absolute;
		top: -17px;
		left: 50%;
		transform: translateX(-50%); }


	&__item {
		margin-bottom: 30px;
		display: flex;
		align-items: center;
		&:last-child {
			margin-bottom: 0; } }
	&__icon {
		width: 37px;
		margin-right: 20px; }
	&__desc {
		p {
			margin: 0; } } }
