.nav {
	display: block;
	transition: 0.4s;
	&.hide {
		opacity: 0;
		visibility: hidden;
		pointer-events: none; }
	&-list {
		display: flex;
		margin: 0;
		padding: 0; }

	&-item {
		margin: 0;
		padding: 0 30px;
		&:after {
			display: none; }
		&--order {
			min-height: 30px;
			padding-left: 50px;
			padding-right: 50px;
			@include flex_center_b; }
		&--book {
			background-image: url('../images/ic_book.png');
			background-repeat: no-repeat;
			background-size: 25px;
			background-position: 10px center; }
		&--shoot {
			background-image: url('../images/ic_shoot.png');
			background-repeat: no-repeat;
			background-size: 25px;
			background-position: 10px center; }
		&--rest {
			background-image: url('../images/ic_rest.png');
			background-repeat: no-repeat;
			background-size: 25px;
			background-position: 10px center; }
		&--spa {
			background-image: url('../images/ic_spa.png');
			background-repeat: no-repeat;
			background-size: 25px;
			background-position: 10px center; }
		&--other {
			background-image: url('../images/ic_others.png');
			background-repeat: no-repeat;
			background-size: 25px;
			background-position: 10px center; } }

	@include bp(1500px) {
		&-item {
			padding: 0 1.5vw;
			&--order {
				padding-left: 65px; } } }

	@include bp(1300px) {
		&-item {
			padding: 0 1vw;
			&--order {
				padding-left: 45px; } } }

	@include bp($point-5) {
		&-item {
			padding: 0 5px;
			&--order {
				padding-left: 45px; } } }

	@include bp($point-4) {
		&-list {
			flex-wrap: wrap; } } }


