.restaurants {
    padding: 55px 0;
    &:first-child {
        padding-top: 0; }

    &-wrapper {
        padding: 55px 0; }
    &__banner {
        margin-bottom: 110px; }

    &__block {
        display: flex;
        justify-content: space-between;
        @include bp(968px) {
            flex-wrap: wrap;
            justify-content: center; }


        .tool-window {
            transform: none;
            left: auto;
            right: calc(100% - 28px);

            &:before {
                transform: none;
                left: auto;
                right: 7px; } } }

    &__slider {
        width: 58%;
        position: relative;

        @include bp(968px) {
            width: 100%;

            .swiper-slide {
                img {
                    width: 100%; } } } }


    &__content {
        width: 42%;
        margin-left: 72px;
        padding-top: 5px;
        font-weight: 500;

        @include bp(1100px) {
            margin-left: 40px; }
        @include bp(968px) {
            width: 100%;
            margin-left: 0; }
        h3 {
            margin: 0 0 30px; }
        p {
            margin: 30px 0 42px;
            color: $gray4;
            font-size: rem(16px);
            line-height: rem(22.4px); }

        .btn {
            margin-right: 46px;

            @include bp(1100px) {
                margin-right: 25px; } }
        .link {
            @include bp(480px) {
                text-align: center;
                display: block;
                margin-top: 15px;
                border: none;
                transition: none;

                &:hover {
                    border: none; } } } }

    &-buttons {
        display: flex;
        align-items: center;

        @include bp($point-2) {
            display: block;
            text-align: center; } }

    &-slider {
        transition: 0.3s;
        &.hide {
            opacity: 0;
            pointer-events: none; } }


    &__tour {
        display: block;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 2;
        opacity: 0;
        pointer-events: none;
        transition: 0.3s;
        border: none;
        outline: none;

        &.active {
            opacity: 1;
            pointer-events: auto; } }


    &__tour-icon {
        display: inline-flex;
        align-items: center;
        padding: 0 18px;
        height: 35px;
        position: absolute;
        right: 14px;
        top: 14px;
        z-index: 3;
        color: $white;
        background-color: rgba(46, 46, 46, 0.3);
        transition: 0.4s ease;
        font-size: rem(16px);
        font-weight: 400;
        cursor: pointer;

        svg {
            width: 24px;
            height: 24px;
            margin-right: 7px;
            fill: $white;
            stroke: $white;
            stroke-width: 0px; }

        &:hover,
        &.active {
            background-color: $orange; }

        @include bp($point-2) {
            padding: 0 8px;
            span {
                display: none; }
            svg {
                margin: 0; } } }

    &-list {
        padding-top: 65px;
        .restaurants {
            padding: 45px 0;
            &:first-child {
                padding-top: 0; } }

        @include bp($point-4) {
            padding-top: 40px; }

        @include bp($point-3) {
            .restaurants {
                &:last-child {
                    padding-bottom: 0; } } }

        @include bp($point-2) {
            .restaurants {
                padding: 35px 0 30px; } } }




    @include bp(968px) {
        padding: 45px 0;
        border-bottom: 1px solid $gray2;
        &:last-child {
            border-bottom: none; }

        &-wrapper {
            padding: 15px 0 40px; }
        &__content {
            padding-top: 30px;



            .details {
                display: none; }

            .btn {
                margin-right: 46px; }

            p {
                margin: 25px 0 30px; } } }

    @include bp($point-2) {
        padding: 30px 0;
        .navigation-arrows {
            display: none; }
        &__content {
            padding-top: 15px;

            h3 {
                margin-bottom: 12px; }

            p {
                font-size: rem(14px);
                margin: 15px 0 20px; } } } }

.details {
    display: flex;
    flex-wrap: wrap;
    width: 80%;
    margin-bottom: 20px;

    &--large {
        width: 100%; }

    @include bp(1150px) {
        width: 100%; }

    &__item {
        margin: 0 0 12px;
        line-height: 140%;
        font-weight: 50s0;
        font-size: rem(16px);
        color: $baseTxt;
        width: 50%;
        display: flex;
        align-items: center;
        @include bp(420px) {
            font-size: 13px; }
        &:last-child {
            margin-bottom: 0; }
        &-icon {
            width: 24px;
            margin-right: 12px; } } }






