.close-icon {
	padding: 6px;
	width: 18px;
	height: 18px;
	color: $close-icon-color;
	cursor: pointer;
	transition: .3s linear;
	box-sizing: content-box;

	&:hover {
		color: $close-icon-color-hover; }

	svg {
		width: 100%;
		height: 100%; } }

.navigation-arrows {
	width: auto;
	min-width: 88px;
	position: relative;
	display: inline-flex;
	align-items: center;
	justify-content: space-between;

	&__item {
		display: flex;
		align-items: center;
		justify-content: center;
		cursor: pointer;
		width: 60px;
		height: 60px;
		transform-origin: 50%;
		color: $black;
		transition: transform .3s linear;
		border-radius: 50%;
		border: 1px solid $orange;

		&:hover {
			color: $orange;

			svg {
				stroke: $orange; } }

		svg {
			width: 20px;
			height: 20px;
			fill: transparent;
			stroke-width: 2px; }

		&:focus {
			outline: none; }

		&:first-child {
			margin-right: 20px;

			&:hover {
				svg {
					transform: translateX(-5px); } } }

		&:last-child {

			&:hover {
				svg {
					transform: translateX(5px); } } } }

	&--white {
		svg {
			stroke: $white; } }

	&--white-bor {
		.navigation-arrows__item {
			border-color: $white;

			&:hover {
				border-color: $orange; } }
		svg {
			stroke: $white; } }

	&--white-black_arrow {
		.navigation-arrows__item {
			background-color: $white;
			border: none;

			// &:hover
			// 	border: 1px solid $orange

			svg {
				stroke-width: 1; } } }

	&--dark {
		.navigation-arrows__item {
			width: 48px;
			height: 48px;
			background-color: rgba(46, 46, 46, 0.3);
			border: none;

			svg {
				stroke: $white;
				width: 17px;
				height: 17px; }

			&:hover {
				background-color: rgba(46, 46, 46, 0.5); } } }

	&--center {
		width: 100%;
		@include center('v');
		left: 0;
		justify-content: space-between;
		padding: 0 15px;
		z-index: 5; }

	&--small {
		.navigation-arrows__item {
			width: 30px;
			height: 30px;

			svg {
				width: 12px;
				height: 12px; } } } }



.swiper-pagination.swiper-pagination-center {
	.swiper-pagination ::v-deep {
		.swiper-pagination-bullet {
			opacity: 1;
			background: green; }
		.swiper-pagination-bullet-active {
			background-color: white; } } }
