.contacts-block {
	&__main-title {
		margin-top: 0; }
	&__contacts-wrapper {
		display: flex;
		justify-content: space-between;
		align-items: start;
		flex-wrap: wrap;
		border-bottom: solid 1px $gray2; }
	&__block-wrapper {
		display: flex;
		justify-content: space-between;
		align-items: start;
		@include bp($point-4) {
			flex-wrap: wrap; } }
	&__block {
		display: flex;
		flex-direction: column;
		margin-bottom: 65px;
		@include bp($point-2) {
			margin-bottom: 35px; }
		&--address {
			max-width: 402px; }
		&--tel {
			padding-left: 30px;
			padding-right: 30px;
			@include bp($point-4) {
				padding-left: 0; } }
		&--mail {
			padding-left: 30px;
			padding-right: 30px;
			@include bp($point-3) {
				padding-left: 0; } } }

	&__title {
		font-size: rem(16px);
		color: $gray4; }
	&__text {
		margin-top: 15px;
		font-size: rem(24px);
		font-weight: bold; }

	&__transport-wrapper {
		@include flex_between_center;
		margin-top: 55px;
		margin-bottom: 20px;
		@include bp($point-4) {
			flex-wrap: wrap;
			margin-left: -30px;
			margin-right: -30px; }
		@include bp(720px) {
			flex-direction: column; } }
	&__transport {
		position: relative;
		@include flex_between_center;
		color: $gray4;
		cursor: pointer;
		&::after {
			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			width: 0;
			height: 3px;
			background-color: $orange;
			transition: width 0.3s ease; }
		&:not(.js-transport-active):hover {
			transform: scale(1.05);
			&::after {
				width: 100%;
				height: 1px;
				transition: width 0.3s ease; } }
		&:active {
			transform: scale(0.95); }

		& svg {
			width: 25px;
			height: 25px;
			fill: $gray4;
			stroke: $white; }
		&--invis {
			@include bp($point-5) {
				display: none; } } }

	&__transport-text {
		margin: 10px 15px; }
	&__map {
		position: relative;
		height: 680px;
		margin-bottom: 80px;
		@include bp($point-4) {
			margin-bottom: 0; } }
	&__map-wrapper {
		height: 680px;
		@include bp($point-4) {
			height: 480px;
			overflow: hidden; }
		@include bp($point-3) {
			height: 360px; } }

	&__form-contacts {
		position: absolute;
		top: 70px;
		left: 17%;
		@include bp($point-4) {
			position: relative;
			top: 0;
			left: 0; } } }

.js-transport-active {
	color: $black;
	opacity: 1;
	transition: opacity 0.3s ease;
	&::after {
		width: 100%;
		transition: width 0.3s ease; }
	& svg {
		fill: $black; } }

.form-contacts {
	width: 320px;
	padding: 30px;
	background-color: $white;
	box-shadow: 0px 18px 60px rgba(0, 0, 0, 0.12);
	@include bp($point-4) {
		width: 100%;
		padding: 60px;
		box-shadow: none; }
	@include bp($point-2) {
		padding: 30px; }
	&__title {
		display: none;
		font-weight: bold;
		opacity: 0;
		transition: opacity 0.3s ease;
		@include bp($point-4) {
			margin-top: 0; } }
	&__row {
		position: relative;
		@include bp($point-4) {
			width: 260px; }
		@include bp($point-2) {
			width: 100%; } }
	&__btn {
		position: absolute;
		width: 45px;
		height: 45px;
		top: 0;
		right: 5px;
		cursor: pointer;
		z-index: 50;
		& svg {
			stroke: $white; }
		&:hover {
			transform: scale(1.1); }
		&:active {
			transform: scale(0.95); } }
	&__text-wrapper {
		display: none;
		opacity: 0;
		transition: opacity 0.3s ease; }
	&__text {
		margin-top: 7px;
		margin-bottom: 7px;
		font-size: rem(12px);
		line-height: 130%; }
	&__form {
		height: 0;
		overflow: hidden;
		transition: height 0.3s ease;
		&.is-active {
			height: 70px;
			overflow: visible;
			transition: height 0.3s ease; } } }

.js-text-active {
	display: block;
	opacity: 1;
	transition: opacity 0.3s ease; }

