.error-block {
	img {
		position: absolute;
		width: 100%;
		height: 100vh;
		margin: 0;
		padding: 0;
		object-fit: cover; } }

.error-message-block {
	position: absolute;
	top: calc(50% - 163px);
	left: calc(40% - 151px);
	max-width: 303px;
	color: $white;
	text-align: center;

	h1 {
		margin: 0 0 45px;
		font-size: 130px;
		line-height: 80%;
		letter-spacing: -0.01em;
		font-weight: bold; }
	p {
		margin: 45px 0; }
	.btn {
		max-width: 160px; } }


