.popup {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	opacity: 0;
	visibility: hidden;
	max-height: 100vh;
	overflow-y: auto;
	overflow-x: hidden;
	z-index: -1;
	pointer-events: none;
	transition: opacity 0.3s linear;


	&__wrapper {
		width: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		min-height: 100%;
		position: relative;
		padding: 50px;

		&:after {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			background: rgba(46, 46, 46, 0.3);
			z-index: 1; }

		@include bp($point-2) {
			padding: 25px; } }



	&__layout {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		transition: all 0.3s linear;
		transition-delay: 0.3s;
		z-index: 2;
		opacity: 0; }


	&__content {
		max-width: 100%;
		width: auto;
		height: auto;
		position: relative;
		color: $black;
		z-index: 5;
		transition-delay: 0s;
		transition: all 0.3s linear;
		opacity: 0; }


	&__close {
		cursor: pointer;
		position: absolute;
		top: -17px;
		right: -47px;
		z-index: 12;
		transition: all 0.3s linear;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 50%;
		width: 48px;
		height: 48px;
		background-color: rgba(46, 46, 46, 0.3);
		border: none;

		svg {
			width: 17px;
			height: 17px;
			stroke: $white;
			stroke-width: 3px; }

		&:hover {
			background-color: $orange;
			svg {
				stroke: $white; } }

		&--login {
			background-color: transparent;
			top: 10px;
			right: 10px;

			svg {
				stroke: $gray4; } }

		@include bp($point-2) {
			width: 38px;
			height: 38px; } }


	&-block {
		padding: 50px 50px 60px;
		background-color: $white;

		@include bp($point-2) {
			padding: 30px; } }

	&-unit {
		max-width: 800px;
		width: 100%;
		background-color: $white;
		min-height: 100vh;
		transition: 1.6s ease;
		transition-delay: .3s;
		transform: translateX(80%);

		@include bp($point-3) {
			transition-delay: 0s; } }

	&-slider {
		@include bp($point-2) {
			.navigation-arrows {
				display: none; } } }

	&-article {
		padding: 40px;
		h1,
		h2,
		h3,
		h4,
		h5,
		h6,
		p,
		ul,
		ol {
			&:first-child {
				margin-top: 0; } }

		@include bp($point-4) {
			padding: 40px 55px 35px; }

		@include bp($point-2) {
			padding: 20px; } }



	&[data-block="ajax-content"] {
		transform: translateX(110%);
		transition: 1.5s cubic-bezier(.73,.54,0,.98);
		opacity: 1;
		.content {
			background-color: $white;
			padding: 30px; }
		.popup__wrapper {
			padding: 0;
			justify-content: flex-end;
			align-items: flex-start; }
		.popup__close {
			position: fixed;
			top: 14px;
			right: 24px; }

		&.is-show {
			transform: translateX(0);

			.popup-unit {
				transform: translateX(0); } }

		@include bp($point-3) {
			transition: 0s;
			transform: none;

			.popup__wrapper {
				&:after {
					display: none; } }

			.popup__close {
				right: 10px;
				top: 10px; } } }




	&[data-block="video"] {
		iframe {
			width: 80vw;
			height: 80vh; }

		@include bp($point-3) {
			iframe {
				width: 100%;
				height: 100vh; }
			.popup__wrapper {
				padding: 0; }
			.popup__content {
				width: 100%; }
			.popup__close {
				position: fixed;
				z-index: 5;
				top: 10px;
				right: 12px; } } }



	&.is-show {
		opacity: 1;
		visibility: visible;
		pointer-events: auto;
		z-index: 100;

		.popup__layout {
			opacity: 0.1;
			transition-delay: 0s; }

		.popup__content {
			transition-delay: 0.3s;
			opacity: 1; } } }

.popup-thank {
	display: none;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 100;

	&.is-show {
		display: flex;
		justify-content: center;
		align-items: center; }

	&__content {
		max-width: 400px;
		width: 100%;
		background-color: $white;
		padding: 30px 50px;
		text-align: center;
		z-index: 101;
		position: relative;
		h4 {
			margin-top: 18px; }
		&--login {
			display: flex;
			flex-direction: column;
			align-items: center;
			max-width: 540px;
			padding: 60px; } }

	&__success-mark {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 80px;
		height: 80px;
		border-radius: 50%;
		border: solid 1px $orange;
		&:after {
			content: '';
			position: relative {
    		top: -3px; }
			width: 30px;
			height: 15px;
			border-left: solid 3px $orange;
			border-bottom: solid 3px $orange;
			transform: rotate(-45deg); } }

	&__btn {
		margin-top: 40px;
		width: 165px; } }



