.entrance-block {
	img {
		position: absolute;
		width: 100%;
		height: 100vh;
		margin: 0;
		padding: 0;
		object-fit: cover; } }

.wrapper-form-entrance {
	position: absolute;
	top: calc(50% - 281px);
	right: 18%;
	width: 476px;
	background-image: url(../images/border-gorm.svg);
	z-index: 1;
	height: 563px;

	@include bp($point-2) {
		background-image: none;
		width: 430px;
		right: calc(50% - 250px); } }



.entrance-form {
	position: absolute;
	top: 63px;
	left: 46px;
	width: 398px;
	background-color: $gray7;
	height: 436px;
	box-shadow: 0 10px 10px rgba(0, 0, 0, 0.3);

	@include bp($point-2) {
		top: 63px;
		left: 46px;
		width: 280px;
		background-color: #faf8f1;
		height: 390px;
		padding: 0 20px 0; }

	.form {
		max-width: 300px;
		margin: 0 0 0 43px;

		@include bp($point-2) {
			margin: 0; } }

	&__title {
		margin: 51px 0 17px;
		h3 {
			margin: 0; } }

	&__description {
		padding-bottom: 13px;
		color: $gray4;
		line-height: 0.5; }
	&__description--invalid {
		display: none;
		width: 221px;
		color: $black;
		padding-bottom: 13px;
		line-height: .5;
		padding-left: 4px;
		small {
			background-color: $beige;
			outline: solid $beige 4px;
			width: 221px;
			border-bottom: 2px solid $beige;
			padding-top: 1px; } }

	.form-bottom {
		button {
			width: 120px;
			height: 45px;
			line-height: 140%; }
		@include bp($point-2) {
			align-items: flex-start; } }

	label.error {
		background-color: $gray7; } }

.form-line {
	width: 100%;
	margin-top: 30px;

	.form-field__label {
		color: $gray4;
		background-color: $gray7; }

	.form-entrance--inactive:before {
		content: "";
		position: absolute;
		top: 0;
		bottom: 0;
		right: 16px;
		margin: auto 0;
		width: 24px;
		height: 24px;
		background-image: url(../images/sprites/svg/frame-focus.svg);
		background-size: cover;
		background-position: top;
		z-index: 3; }
	//.form-entrance--active:before
	.form-field__btn--send {
		//content: ""
		display: none;
		position: absolute;
		top: 0;
		bottom: 0;
		right: 16px;
		margin: auto 0;
		width: 24px;
		height: 24px;
		background-image: url(../images/news-img/icon-valid-mail.svg);
		background-size: cover;
		background-position: top;
		z-index: 3; }
	.form-entrance--active {
		.form-field__btn--send {
			display: block; } } }
