.shooting {
    &__types {
        padding: 30px 0 10px;

        @include bp(968px) {
            padding: 10px 0 0; }

        @include bp($point-2) {
            padding-bottom: 25px; } }

    &__types-list {
        margin: 0 -22px; }
    &-column {
        padding: 0 21px;
        margin-bottom: 45px;
        @include bp(968px) {
            width: 100%;
            img {
                width: 100%; } }

        @include bp($point-2) {
            margin-bottom: 25px;
            &:last-child {
                margin-bottom: 0; } } }
    &-type {
        position: relative;
        display: block;
        &__desc {
            position: absolute;
            bottom: 0;
            left: 0;
            padding: 50px;
            width: 100%;
            @include bp(1140px) {
                padding: 40px; }
            @include bp(968px) {
                padding: 50px; }
            @include bp($point-2) {
                padding: 30px; }
            h3 {
                color: $white;
                margin-bottom: 35px;
                @include bp(540px) {
                    margin-bottom: 25px;
                    line-height: 100%; }
                @include bp(320px) {
                    margin-bottom: 15px; } }
            .btn {
                padding: 0 55px;
                @include bp(480px) {
                    padding: 0 40px;
                    width: auto; } } }
        &__img {
            img {
                display: block; } } }



    &__rules {
        padding: 40px 0;

        @include bp(968px) {
            padding: 25px 0; }

        @include bp(480px) {
            padding: 15px 0; }

        &-list {
            margin: 0 -16px; } }
    &__rule {
        display: block;
        background: $gray1;
        padding: 59px 45px 33px;
        height: 100%;
        color: $baseTxt;
        border: none;

        &:hover {
            color: $orange; }
        @include bp(1140px) {
            padding: 35px 35px 30px; }


        @include bp(968px) {
            display: flex;
            align-items: center; }

        @include bp($point-2) {
            padding: 30px 20px; }
        &-block {
            padding: 0 16px;
            @include bp(968px) {
                width: 100%;
                margin-bottom: 15px; } }
        svg {
            width: 40px;
            height: 40px;
            display: inline-block; }
        svg:nth-child(2) {
            width: 21px;
            height: 19px; }
        p {
            line-height: 22.4px;
            max-width: 220px;

            @include bp(968px) {
                margin: 10px 0; } } }
    &__rule-icon {
        @include bp(968px) {
            min-width: 70px;
            max-width: 70px;
            margin-right: 20px; }

        @include bp($point-2) {
            margin-right: 10px; } }
    &-room {
        padding: 40px 0 90px;


        &__block {
            width: 100%;
            min-height: 300px;
            position: relative;
            overflow: hidden;
            color: $white;
            @include foil;
            background-image: url('../images/baner-2.jpg'); }
        &__desc {
            position: relative;
            padding: 50px 50px 20px 50px;
            max-width: 600px; }

        h3 {
            margin: 0 0 20px; }
        p {
            line-height: 22.4px;
            font-size: 16px;
            margin-bottom: 30px; }

        &--activity {
            .shooting-room__desc {
                max-width: 800px;

                p {
                    max-width: 595px;

                    a {
                        color: inherit;

                        &:hover {
                            color: $orange; } } } } }

        @include bp(968px) {
            padding-top: 25px;
            &__desc {
                padding: 90px 50px 50px; } }

        @include bp($point-2) {
            padding-bottom: 70px;
            padding-top: 0;
            &__desc {
                padding: 75px 30px 30px;

                .btn {
                    width: auto; } } } } }






