.types-placement {
    padding: 30px 0;
    &__list {
        margin: 0 -30px;
        padding: 0;
        @include flex_center_v;
        li {
            margin: 0;
            padding: 0 30px;
            &:after {
                display: none; } } }
    &__link {
        font-size: rem(18px);
        line-height: 160%;
        color: $gray4;
        padding-bottom: 10px;

        &:hover,
        &.active {
            border-bottom: 3px solid $orange;
            color: $baseTxt; } }

    @include bp(610px) {
        padding: 20px 0;
        &__list {
            margin: 0 -5px;
            justify-content: space-between;

            li {
                padding: 0 5px; } }

        &__link {
            font-size: 3.5vw;
            line-height: 120%; } } }


.placement {
    padding-top: 88px;
    position: relative;

    @include bp($point-2) {
        padding-top: 66px; }
    &-nav {
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background-color: $white;
        &.fixed {
            position: fixed;
            top: 65px;
            z-index: 30;

            @include bp($point-2) {
                top: 63px; } } }
    &__types-heading {
        display: flex;
        border-bottom: 1px solid $gray3;
        flex-wrap: wrap;
        .col {
            width: 50%;
            @include bp(1024px) {
                width: 100%; }

            p {
                margin: 0 0 35px 0;
                color: $gray4;
                font-size: 16px;
                line-height: 140%;
                @include bp(1024px) {
                    margin-bottom: 15px; } } } }
    &__line {
        display: flex;
        h3 {
            margin-top: 0; } }

    &-stars {
        margin-left: 10px;
        padding-top: 7px;
        img {
            width: 10px; } }

    &__type {
        padding: 65px 0;

        @include bp($point-4) {
            padding: 40px 0;

            &:last-child {
                padding-bottom: 75px; } }

        @include bp($point-2) {
            padding-top: 20px; } }


    &__block {
        display: flex;
        margin-bottom: 80px;
        @include bp(1024px) {
            flex-wrap: wrap; } }

    &__slider {
        width: 58%;
        margin-bottom: 30px;
        @include bp(1024px) {
            width: 100%; }
        &-item {
            &__block {
                position: relative; }
            &__label {
                @include flex_center_b;
                position: absolute;
                top: 0;
                right: 0;
                margin: 15px;
                background: rgba(0, 0, 0, 0.5);
                z-index: 10;
                p {
                    margin: 7.25px 18px 7.25px 0;
                    color: $white;
                    line-height: 22.4px;
                    font-size: 16px;
                    font-weight: 400; }

                svg {
                    display: inline-block;
                    width: 24px;
                    height: 13px;
                    stroke: $white;
                    stroke-width: 1px;
                    stroke-opacity: 0;
                    margin: 0 9.5px 0 18px; } } } }

    &__desc {
        width: 42%;
        padding-left: 70px;
        @include bp(1050px) {
            padding-left: 45px; }
        @include bp(1024px) {
            width: 100%;
            padding-left: 0; } }


    &-icon {
        position: relative;
        margin: 0 6px;
        &.disabled {
            img {
                opacity: 0.5; } } }

    &__bedsize {
        display: flex;
        margin-top: 10px; } }



.desc {
    &-top {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin-bottom: 15px;
        h3 {
            margin: 0px 50px 0 0; } }
    p {
        font-size: rem(16px);
        line-height: 140%;
        color: $baseTxt; }

    p {
        margin: 0;
        line-height: 22.4px; }
    .btn {
        margin: 0 40px 0 0; } }



.amenities {
    margin: 30px 0 30px 0;
    &__list {
        margin: 0 -5px;
        padding: 0;
        @include flex_center_v;
        flex-wrap: wrap;

        li {
            margin: 0 0 16px 0!important;
            padding: 0 5px;
            @include flex_center_v;
            width: 50%;
            &:after {
                display: none; }
            svg {
                width: 24px;
                height: 24px;
                stroke-width: 1px;
                stroke: $baseTxt;
                margin-right: 15px; }
            p {
                margin: 0;
                line-height: 140%; } } } }



