.page-banner {
	width: 100%;
	background-repeat: no-repeat;
	background-position: top center;
	margin-bottom: 50px;

	&--shoting {
		.page-banner__block {
			max-width: 1000px; } }

	&--placement {
		margin-bottom: 0;
		.page-banner__block {
			max-width: 1377px; } }

	&--activity {
		.page-banner__block {
			max-width: 1250px; } }

	&__block {
		@include flex_center_b;
		height: 300px;
		color: $white;
		text-align: center;
		max-width: 1058px;
		margin: 0 auto;

		h2 {
			margin: rem(30px) 0; } }

	@include bp(1100px) {
		h2 {
			font-size: 5.3vw; } }

	@include bp($point-3) {
		margin-bottom: 25px;

		&--placement {
			margin-bottom: 0; }

		h2 {
			font-size: 4.8vw; } }

	@include bp($point-2) {
		h2 {
			font-size: 7.5vw; } } }

