.club-backing-card {
	img {
		position: absolute;
		top: 0;
		right: 5%;
		z-index: -99; } }

.club-card {
	margin-top: 81px;

	@include bp($point-5) {
		margin-top: 65px; }

	h1 {
		margin: 0;
		padding-bottom: 30px;
		border-bottom: 1px solid $gray2;

		@include bp($point-5) {
			border-bottom: none; } } }

.club__description {
	display: flex;
	justify-content: space-between;

	@include bp($point-5) {
		flex-direction: column;
		max-width: 	475px;
		margin: 10px auto 0; }
	p {
		max-width: 570px;
		margin: 60px 50px 25px 0;

		@include bp($point-5) {
			margin: 24px 0 5px 0; } }

	.club-image {
		h3 {
			margin: 60px 0 0 0; } } }

.club-block {
	display: flex;
	justify-content: space-between;
	margin-top: 40px;
	padding-bottom: 40px;

	@include bp($point-5) {
		flex-direction: column;
		max-width: 475px;
		margin: 60px auto 0; }

	.club-block__image {
		border: none; } }



.club-block__desc {
	max-width: 570px;
	margin-right: 50px;
	@include bp($point-5) {
		margin-right: 0; }

	h3 {
		margin-top: 0;

		@include bp($point-5) {
			margin: 30px 0 24px 0; } }
	p {
		margin-bottom: 0;
		padding-bottom: 40px;
		border-bottom: 1px solid $gray2;
		@include bp($point-3) {
			padding-bottom: 0;
			border: none; } } }

.club-block__buttons {
	margin-top: 40px;

	@include bp($point-5) {
		margin-top: 30px; }

	.btn--orange {
		min-width: 180px;
		margin-right: 15px;

		@include bp($point-3);
		margin-bottom: 20px; }

	.btn--bor {
		min-width: 172px;
		height: 55px; } }

.present-block {
	position: relative;
	margin-top: 70px;
	padding-top: 20px;
	background-color: $black;

	h3 {
		color: $white; } }

.price-block {
	display: flex;
	justify-content: space-between;
	margin-top: 60px;
	padding-bottom: 215px;

	@include bp($point-5) {
		flex-direction: column;
		max-width: 475px;
		margin: 0 auto;

		.price-item {
			margin-top: 80px; } }

	.price-item__img {
		border: none;

		img {
			@include bp($point-5) {
				width: 100%; } } } }

.price-item__price {
	display: flex;
	justify-content: space-between; }

.price-item__price {
	h5 {
		color: $white;
		margin-top: 18px; } }

.price-item__buy {
	display: flex;
	border: none;
	img {
		width: 20px;
		height: 17px;
		margin: 23px 9px 0 0; }
	p {
		margin-top: 20px;
		color: $orange2; } }

.present-img {
	img {
		position: absolute;
		bottom: -22px;
		right: calc(50% - 161px); } }

// Start ajax-cards style

.cards-details {
	padding-top: 65px;

	.cards-details-wrapper {
		max-width: 740px;
		padding: 0 40px;

		@include bp($point-3) {
			padding: 0 20px; } } }

.cards-detals-header {
	display: flex;
	justify-content: space-between;
	padding-bottom: 30px;

	img {
		max-width: 360px;

		@include bp($point-3) {
			max-width: 100%; } } }

.card-detals__description {
	margin-left: 45px;

	h3 {
		margin: 35px 0 20px; }
	h2 {
		font-size: $h2-fs-480;
		margin: 35px 0 20px; }
	p {
		margin-bottom: 16px; }

	.card-details--prise {
		h3 {
			margin-top: 30px; } } }

.cards-detals-list {
	ol {
		margin-top: 16px; }
	li {
		margin: 16px 0; } }

.cards-detals-form {
	padding-top: 35px;
	padding-bottom: 93px;

	.form-field__label {
		color: $gray4;
		background-color: #fff; }

	.form-col--email {
		width: 66%;

		@include bp($point-3) {
			width: 100%; } }

	.form-col--card-buy {
		width: 48%;

		@include bp($point-3) {
			width: 100%; } }

	.form-line {
		margin-top: 0; }

	textarea {
		height: 90px;
		resize: none; }

	.form-bottom {
		@include bp($point-3) {
			flex-direction: row;

			button {
				align-self: flex-start; } } }

	.form-bottom--buy {
		justify-content: flex-start;

		.form-checkbox {
			margin-left: 65px; } }

	.form-checkbox {
		max-width: 395px;

		@include bp($point-3) {
			margin-left: 20px; }

		small {
			color: $gray4; }

		.form-checkbox__checkmark {
			top: 1px;
			width: 22px;
			height: 22px; } }

	.btn--large {
		width: 120px;
		height: 45px; } }

