.my-orders {
	&__main-block {
		margin-top: 25px;
		margin-bottom: 50px; } }

.main-block {
	@include flex_center_b;
	flex-direction: column;
	border: 7px solid $gray1;
	padding-top: 35px;
	padding-right: 65px;
	padding-bottom: 30px;
	padding-left: 65px;
	@include bp($point-4) {
		padding: 50px 30px; }
	&__content {
		@include flex_between_center;
		align-items: start;
		flex-wrap: wrap;
		width: 100%;
		padding-top: 30px;
		padding-bottom: 30px;
		border-bottom: solid 1px $gray2; }
	&__img-wrapper {
		width: 150px;
		margin-right: 45px; }
	&__description {
		padding-right: 20px;
		margin-right: auto;
		& h5 {
			margin-top: 10px; } }
	&__details {
		min-width: 425px;
		& p {
			margin: 10px 0; }
		@include bp($point-4) {
			min-width: 300px; }
		@include bp($point-2) {
			min-width: 200px; } }
	&__total {
		@include flex_between_center;
		flex-wrap: wrap;
		width: 100%;
		& span {
			@extend h5;
			font-weight: bold; } }
	&__number {
		margin-left: 195px;
		margin-right: auto;
		padding-right: 20px;
		@include bp(1150px) {
			margin-left: 0; } }
	&__price {
		min-width: 200px;
		margin-right: 48px; }
	&__title-link {
		color: inherit;
		border: none;
		&:hover {
			color: $orange;
			border: none; } } }

